import React from "react";
import Card from "./Card";

const listings = ({ listings }) => {
  const getListings = () => {
    let listingsOnPage = [];
    let result = [];

    listings.map((listing) => {
      return listingsOnPage.push(
        <Card
          title={listing.title}
          address={listing.address}
          city={listing.city}
          province={listing.province}
          price={listing.price}
          sale_choice={listing.sale_choice}
          houseType={listing.houseType}
          bedrooms={listing.bedrooms}
          baths={listing.baths}
          sqft={listing.sqft}
          photoMain={listing.photoMain}
          slug={listing.slug}
        />
      );
    });

    for (let i = 0; i < listings.length; i += 3) {
      result.push(
        <div className="row">
          <div className="col-1-of-3">{listingsOnPage[i]}</div>
          <div className="col-1-of-3">
            {listingsOnPage[i + 1] ? listingsOnPage[i + 1] : null}
          </div>
          <div className="col-1-of-3">
            {listingsOnPage[i + 2] ? listingsOnPage[i + 2] : null}
          </div>
        </div>
      );
    }

    return result;
  };

  return <div>{getListings()}</div>;
};

export default listings;
